.navbar-container {
	padding: 40px;	
}

.inline-block {
	display: inline-block;
}

.navbar-logo {
	font-family: Crimson Text, serif;
	font-size: 64px;
	font-weight: bold;
	margin: 0;
	margin-right: 10px;
}

.navbar-logo-text {
	font-family: Work Sans, sans-serif;
	font-size: 24px;
	margin: 0;
}

.navbar-menu {
	width: 300px;
	margin: 0 auto;
	display: flex;
	justify-content: space-between;
	padding: 40px 0;
	text-align: center;
}

.navbar-menu a {
	color: white;
	font-family: Work Sans, sans-serif;
	text-decoration: none;
	font-weight: bold;
	margin: 0;
}

.navbar-menu a::after {
	content: " ";
	width: 12px;
	display: inline-block;
	border: 3px solid white;
	position: relative;
	top: 1.25em;
	left: -50%;
	opacity: 0.1;
	transition: 500ms ease-out;
}

.navbar-menu a:hover:after {
	border: 3px solid white;
	opacity: 1;
}
